import * as ACTION_TYPES from './action_types'
import {IPost,IProfile, ICategory} from '../../types/blog';

export interface IDefaultItems<T> {
  isLoading: boolean,
  isLoaded:boolean,
  items: T[] | null,
  hasError:boolean
}
interface IDefaultItemsPayload<T> {
  (options?:Partial<T>):T
}
function defaultItemsPayload<T>(options?:Partial<T>){
  const defaults={
    isLoading: false,
    isLoaded:false,
    items: null,
    hasError:false
}
return payloadInit(defaults,options)
}
export type IAuthPayload ={
  isAuthenticated: boolean,
  profile: IProfile | null,
  tokens:ITokens | null,
  loginErrors: any
}

export type ICategoryPayload = IDefaultItems<ICategory>;
export type IPostPayload = IDefaultItems<IPost>;


export type IFormPayload= {
  user_textChange: string,
  user_textSubmit: string
}
export interface IpayloadInit<T> {
  (defaults:T,options?:Partial<T>):T
}
export function payloadInit<T>(defaults:T,options?:Partial<T>):T{
return {...defaults,...options}
}

export const  categoryPayloadInit :IDefaultItemsPayload<ICategoryPayload>= defaultItemsPayload;
export const postPayloadInit:IDefaultItemsPayload<IPostPayload> = defaultItemsPayload;

// initilizer
export function authPayloadInit(options?:Partial<IAuthPayload>):IAuthPayload{
  const defaults = {
    isAuthenticated : false,
    tokens:null,
    profile:null,
    loginErrors:null
  }
  return payloadInit(defaults,options)
}
export function formPayloadInit(options?:Partial<IFormPayload>):IFormPayload{
  const defaults = {
    user_textChange: "",
  user_textSubmit: ""
  }
  return payloadInit(defaults,options)
}
export interface ITokens {
  accessToken?: string;
  accessTokenExpireAt?: number;
}
export interface IActionType<P>{
  type:string;
  payload?: P | {}
}
export type IAuthActionType = IActionType<IAuthPayload>;
export type IPostActionType = IActionType<IPostPayload>;
export type ICategoryActionType = IActionType<ICategoryPayload>;
export type IFormActionType = IActionType<IFormPayload>;


export function actionType<P>(type:string,payload?:P){
  return{
  type:type,
  payload: payload
  }
}

export const login_success = (payload:IAuthPayload):IAuthActionType => {
  return {
    type: ACTION_TYPES.LOGIN_SUCCESS,
    payload:  payload
  }
}

export const login_failure = (errors:any):IAuthActionType => {
  return {
    type: ACTION_TYPES.LOGIN_FAILURE,
    payload:authPayloadInit({loginErrors:errors})
  }
}
export const logout = ():IAuthActionType => {
  return {
    type: ACTION_TYPES.LOGOUT,
    payload:authPayloadInit()
  }
}

export const SUCCESS = {
  type: ACTION_TYPES.SUCCESS
}

export const FAILURE = {
  type: ACTION_TYPES.FAILURE
}

export const success = () => {
  return {
    type: ACTION_TYPES.SUCCESS
  }
}

export const failure = () => {
  return {
    type: ACTION_TYPES.FAILURE
  }
}

export const user_input_change = (text:any) => {
  return {
    type: ACTION_TYPES.USER_INPUT_CHANGE,
    payload: text
  }
}

export const user_input_submit = (text:any) => {
  return {
    type: ACTION_TYPES.USER_INPUT_SUBMIT,
    payload: text
  }
}


export const set_items = (payload:any) => {
  return {
    type: ACTION_TYPES.SET_ITEMS,
    payload: payload
  }
}

export const remove_items = () => {
  return {
    type: ACTION_TYPES.REMOVE_ITEMS,
    payload: {}
  }
}

export const set_isLoading = () => {
  return {
    type: ACTION_TYPES.FETCH_REQUEST,
    payload: {isLoading:true}
  }
}

export const set_hasError = () => {
  return {
    type: ACTION_TYPES.HAS_ERRORR,
    payload: {hasError:true}
  }
}

