import React from 'react';
import { Route, BrowserRouter, Routes, Navigate} from 'react-router-dom';
//import history from './utils/history';

import {useSiteContext} from './context';
import Home from '../pages/Home';
import { ForgotPassword } from '../pages/ForgotPassword';
import Login from '../pages/Login';
import { NotFound } from '../pages/NotFound';
import { Lock } from '../pages/Lock';
import { ServerError } from '../pages/ServerError';
import Signup from '../pages/Signup';
import UserProfile from '../pages/UserProfile'
import Contactme from '../pages/contact'
import Portfolio from '../pages/portfolio'
import Layout from '../components/Layout';

import Profile from '../Profile/profile';
import ShowUser from '../Profile/showuser';
import SendMessage from '../Profile/sendmessage';
import ShowMessages from '../Profile/showmessages';
import ReplytoMessage from '../Profile/replytomessage';

import Blog from '../Blog/blog';
import {Posts} from '../Blog/posts';
import AddPost from '../Blog/addpost';
import ShowPost from '../Blog/showpost';
import EditPost from '../Blog/editpost';

import Test from '../pages/test';


const PrivateRoute = (props: React.PropsWithChildren<{ auth: boolean }>) =>  <>props.auth ? props.children : <Navigate to='/login' /></>;

const AppRoutes = () => {
  const context = useSiteContext()

  return (
      <BrowserRouter>
        {/*<Header />*/}
        <Routes>
          <Route path='/' element={<Layout />}>
            <Route index element={<Home />} />
            <Route path='test' element={<Test />} />
            <Route path='portfolio' element={<Portfolio />} />
            <Route path='forgetPassword' element={<ForgotPassword />} />
            <Route path='notfound' element={<NotFound />} />
            <Route path='servererror' element={<ServerError />} />
            <Route path='lock' element={<Lock />} />
            <Route path='profile' element={<UserProfile />} />
            <Route path='login' element={<Login />} />
            <Route path='signup' element={<Signup />} />
            <Route path='hireme' element={<Contactme />} />
            <Route path='contact' element={<Contactme />} />
            <Route path='*' element={<NotFound />} />

            <Route path="user/:name" element={<ShowUser />} />

            <Route path='profile' element={<Profile />} />
            <Route path='profile' element={<PrivateRoute auth={context.states?.auth?.state.isAuthenticated!}> <Profile /> </PrivateRoute>} />
            
            <Route  path='blog'  element={<Blog />}> 
                  <Route path='post/:pid' element={<ShowPost />} /> 
                  <Route path='editpost/:pid' element={<EditPost />} />
                  <Route path='addpost' element={<AddPost />} />
                  
                  <Route  path='posts' element={<Posts />}/>                 
            </Route>
            <Route path="/sendmessage" element={<PrivateRoute auth={context.states?.auth.state.isAuthenticated!}> <SendMessage />  </PrivateRoute>} />
            <Route path="/showmessages/:mid" element={<PrivateRoute auth={context.states?.auth.state.isAuthenticated!}> <ShowMessages /> </PrivateRoute>} />
            <Route path="/replytomessage" element={<PrivateRoute auth={context.states?.auth.state.isAuthenticated!}> <ReplytoMessage /> </PrivateRoute>} />
          </Route>
        </Routes>
      </BrowserRouter>
  )
}

export default AppRoutes;
