import { Link } from 'react-router-dom';
import hero from '../assets/images/last.png';
import MaroonCard from '../components/MaroonCard'
export default function Banner(){

    return (
      
<MaroonCard size='xl' textSize='lg' title='Welcome'>
  
    <div className="row flex-lg-row-reverse align-items-center g-5 pb-4">
      <div className="col-10 col-sm-8 col-lg-6">
        <img src={hero} className="d-block mx-lg-auto img-fluid" alt="Bootstrap Themes" width="700" height="500" />

      </div>
      <div className="col-lg-6">
        <h3 className="display-6 fw-bold lh-1 mb-3">Welcome to my virtual space .</h3>
        <p className="lead">Whether you are Recruiter, employer, developer, engineer, learner or agent,whoever you are, you came to the right place to fullfil your mission to learn about me or my content.</p>
        <p>Recomanded content ...</p>
        <div className="d-grid gap-2 d-md-flex justify-content-md-start">
          
          <Link to="/blog/posts" className="btn btn-primary "><i className="bi bi-newspaper"> Blog </i> </Link>
          <Link to="/portfolio" className="btn btn-info "><i className="bi bi-person-workspace"> Portfolio </i> </Link>
          <Link to="/hireme" className="btn btn-success "><i className="bi bi-emoji-heart-eyes"> Hire me</i></Link>
        </div>
      </div>
    </div>

  </MaroonCard>
    );
}
