import history from '../utils/history';
import {useSiteContext } from '../utils/context';
import { Row, Col, Button } from 'react-bootstrap';
import MaroonCard from '../components/MaroonCard';
import { FormBuilder } from '../utils/boostrapForms';



const EditPost = (props:any) => {
  const context = useSiteContext()

const handleCleanData = (cleanData: any) =>{
  const user_id = context.states?.auth?.state.profile!._id
  const username = context.states?.auth?.state.profile!.username
  const pid = props.location.state.post.post._id
  const title = cleanData.title
  const body = cleanData.body

  const data = {title: title,
                body: body,
                pid: pid,
                uid: user_id,
                username: username
               }
  context.ajax!.post!('posts/'+data.pid, data)
    .then(res => console.log(res))
    .catch(err => console.log(err))
    .then(()=> setTimeout(() => history.replace('/blog/posts'), 700 ))
}
    return(
<Row className="justify-content-md-center">
      <Col md="8" className="mx-auto">
        <MaroonCard title='Edit Post'>
          {FormBuilder({
            elements: [
              {
                lable: "Post Title",
                attrs: {
                  input: {
                    name: "title",
                    type: "text",
                    required: true,
                    placeholder: "Post title",
                    minLength: 3,
                    maxLength: 100
                  }
                }
              },
              {
                lable: "Post Body",
                attrs: {
                  input: {
                    name: "body",
                    required: true,
                    placeholder: "Post Body",
                    as: "textarea",
                    minLength: 3,
                    style: { height: '200px' }
                  }
                }
              },
            ],
            form: {
              attrs: {
                id: "editPostForm",
                name: "editPostForm"
              },
              cleandata: handleCleanData
            },
            addSubmit: true,
            extra: <Button variant='warning' className='mx-2' onClick={() => history.back()}> Cancel </Button>
          })}

        </MaroonCard>
      </Col>
    </Row>
    )}



export default EditPost;
