import { useState, useEffect} from 'react';

import { Link , useLocation,useParams} from 'react-router-dom';
import history from '../utils/history';
import {useSiteContext } from '../utils/context';
import moment from 'moment';
import { IPost, IComment } from '../types/blog';


const ShowPost = (props: any) => {
  const context = useSiteContext();
const location :any = useLocation();
const { pid } = useParams();
  const [stateLocal, setState] = useState<{
    comment: '',
    fetched: boolean,
    cid: number,
    delete_comment_id: string | null,
    edit_comment_id: string | null,
    edit_comment: string,
    comments_arr: [] | any,
    cur_user_id: any,
    like_post: boolean,
    likes: number,
    //like_user_ids: [],
   // post_title: string | any,
   // post_body: string | any,
   // post_author: string | any,
    //post_id: string | any
    post:IPost | any
  }>({
    comment: '',
    fetched: false,
    cid: 0,
    delete_comment_id: null,
    edit_comment_id: null,
    edit_comment: '',
    comments_arr: null,
    cur_user_id: null,
    like_post: true,
    likes: 0,
    //like_user_ids: [],
    //post_title: null,
    //post_body: null,
    //post_author: null,
    //post_id: null,
    post:null
  })

// check if post in location and set state
  useEffect(() => {
    if (location.state && location.state.post) {
      
      setState({
        ...stateLocal,
        fetched: true,
        post:location.state.post
      })
    }else if (!stateLocal.post && pid){
    context.ajax?.get('/posts/'+ pid).then(data_res=> data_res.item)
        .then(data => data && setState({...stateLocal,fetched: true,post:data}))
        .catch((err) => console.log(err))
    }
  }, [pid])

// sortout comments 
  useEffect(() => {
    if (!stateLocal.comments_arr && stateLocal.post) {
        context.ajax?.get('/comments',{ post_id: stateLocal.post._id } )
        .then(data_res=> data_res.items)
        .then(res => res && setState({ ...stateLocal, comments_arr: [...res] }))
        .catch((err) => console.log(err))
    }
  }, [context.ajax,stateLocal])


  const handleCommentSubmit = (submitted_comment: any) => {
    if (stateLocal.comments_arr) {
      setState({
        ...stateLocal, comments_arr: [submitted_comment,
          ...stateLocal.comments_arr]
      })
    } else {
      setState({ ...stateLocal, comments_arr: [submitted_comment] })
    }
  };

  const handleCommentUpdate = (comment: any) => {
    const commentIndex = stateLocal.comments_arr.findIndex((com: any) => com.cid === comment.cid)
    var newArr = [...stateLocal.comments_arr]
    newArr[commentIndex] = comment

    setTimeout(() => setState({ ...stateLocal, comments_arr: [...newArr], edit_comment_id: null }), 100)
  };


  const handleCommentDelete = (cid: any) => {
    setState({ ...stateLocal, delete_comment_id: cid })
    const newArr = stateLocal.comments_arr.filter((com: any) => com.cid !== cid)
    setState({ ...stateLocal, comments_arr: newArr })
  };

  const handleEditFormClose = () => {
    setState({ ...stateLocal, edit_comment_id: null })
  }


  const RenderComments = (comment: IComment) => {
    return (
      <div className={stateLocal.delete_comment_id === comment._id!
        ? "FadeOutComment"
        : "CommentStyles"}>
        <div>
          <p>{comment.body} </p>
          <small>
            {comment.updatedAt!.toString() === 'Jsut Now'
              ? <div> {props.isEdited
                ? <span> Edited </span>
                : <span> Just Now </span>}</div>
              : comment.createdAt!.toString()
            }
          </small>
          <p> By: {comment.author.firstname + ' ' +comment.author.lastname} </p>
        </div>
        <div>
          {props.cur_user_id === comment.author._id
            ? !props.isEditing
              ? <div>
                <button type='button' className='btn btn-outline-primary' onClick={() => setState({
                  ...stateLocal,
                  edit_comment_id: comment._id!,
                  edit_comment: comment.body
                })
                }>
                  Edit
                </button>
              </div>
              : <form onSubmit={(event: any) => handleUpdate(event, props.comment.cid)}>
               <div className='form-group mb-3'> 
               <input
                className='form-control'
                  aria-label='jhjk'
                  autoFocus={true}
                  name="edit_comment"
                  id="editted_comment"
                  // label="Comment"
                  value={stateLocal.edit_comment}
                  onChange={handleEditCommentChange}
                />
                </div>
                <br />
                <div className='form-group mb-3'>
                <button type='submit' className='btn btn-outline-primary'>
                 
                  Agree
                </button> 
                <button type="button"  className='btn btn-outline-info' onClick={handleEditFormClose}>
                  Cancel
                </button>
                <button type="button" className='btn btn-outline-danger' onClick={() => handleDeleteComment(comment._id)}>
                  Delete
                </button>
                </div>
              </form>
            : null}
        </div>
      </div>
    );
  }

  const handleEditCommentChange = (event: any) => (
    setState({
      ...stateLocal,
      edit_comment: event.target.value
    })
  );

  const handleSubmit = (event: any) => {
    event.preventDefault()
    setState({ ...stateLocal, comment: '' })

    const comment = event.target.comment.value
    const user_id = context.states?.auth?.state.profile!._id
    const username = context.states?.auth?.state.profile!.username
    const post_id = stateLocal.post._id
    const current_time = "Just Now"
    const temp_cid = Math.floor(Math.random() * 1000);

    const submitted_comment = {
      cid: temp_cid,
      comment: comment,
      user_id: user_id,
      author: username,
      date_created: current_time
    }

    const data = {
      comment: event.target.comment.value,
      post_id: post_id,
      user_id: user_id,
      username: username
    }

   context.ajax?.post('/comments', data)
      .then(res => console.log(res))
      .catch((err) => console.log(err))
    window.scroll({ top: 0, left: 0, behavior: 'smooth' })
    handleCommentSubmit(submitted_comment)
  }

  const handleUpdate = (event: any, cid: any) => {
    event.preventDefault()
    console.log(event)
    console.log(cid)
    const comment = event.target.editted_comment.value
    const comment_id = cid
    const post_id = stateLocal.post._id
    const user_id = context.states?.auth.state.profile!._id
    const username = context.states?.auth.state.profile!.username
    const isEdited = true
    const current_time = "Just Now"

    const edited_comment = {
      cid: comment_id,
      comment: comment,
      user_id: user_id,
      author: username,
      date_created: current_time,
      isEdited: isEdited
    }

    const data = {
      cid: comment_id,
      comment: comment,
      post_id: post_id,
      user_id: user_id,
      username: username
    }

    context.ajax?.put('/api/put/commenttodb', data)
      .then(res => console.log(res))
      .catch((err) => console.log(err))
    handleCommentUpdate(edited_comment);
  }

  const handleDeleteComment = (cid: any) => {
    const comment_id = cid
    console.log(cid)
    context.ajax?.delete('/api/delete/comment', { data: { comment_id: comment_id } })
      .then(res => console.log(res))
      .catch((err) => console.log(err))
    handleCommentDelete(cid)
  }

  const handleLikes = () => {
    const user_id: never = context.states?.auth.state.profile!._id as never;
    const post_id = stateLocal.post._id

    const data = { uid: user_id, post_id: post_id }
    console.log(data)
    if (!stateLocal.post.like_user_ids.includes(user_id)) {
      context.ajax?.put('/api/put/likes', data)
        .then(() => !stateLocal.post.like_user_ids.includes(user_id)
          && stateLocal.like_post
          ? setState({
            ...stateLocal,
            likes: stateLocal.post!.likes! + 1,
            like_post: false
          })
          : null)
        .catch(err => console.log(err))
    };
  }


  return (
    
<article className='blog-post'>
   
        {stateLocal.post ?<>
          <h3 className="blog-post-title pb-3 mb-3 fst-italic">
          {stateLocal.post.title}
        </h3>
        <p className="blog-post-meta">{ moment(stateLocal.post.createdAt).format('MMMM Do, YYYY | h:mm a') }  <i className="bi bi-hand-thumbs-up text-success"></i> likes <strong>{stateLocal.post.likes} </strong>
        | By:  <Link  to={"/profile/" + stateLocal.post.author?._id} state={stateLocal.post}>
                   { stateLocal.post.author?.firstname! + ' '+ stateLocal.post.author?.lastname!}
                   </Link>
        </p>
    
          <p className='lead'> {stateLocal.post.body}</p>
          </>
          : context.loader!()
        }

        <div style={{ cursor: 'pointer' }} onClick={context.states?.auth?.state.isAuthenticated
          ? () => handleLikes()
          : () => history.replace('/login')}>
          <i className="material-icons">thumb_up</i>
          <small className="notification-num-showpost">
            {stateLocal.likes}
          </small>
        </div>

      <div>

        <h2> Comments:</h2>
        {stateLocal.comments_arr
          ? stateLocal.comments_arr.map((comment: any) =>
            <RenderComments  {...comment}
              cur_user_id={context.states?.auth?.state.profile! && context.states?.auth?.state.profile!._id }
              key={comment._id}
              isEditing={comment._id === stateLocal.edit_comment_id
                ? true
                : false}
            />)
          : context.loader!()
        }
      </div>
      <div>
        <form onSubmit={handleSubmit}>
          <div className='form-group b=mb-3'>
          <textarea
          className='form-control'
            aria-label='Comment'
            id="comment"
          //label="Comment"
          rows={2}
          placeholder='add comment'
          />
          <br />
          </div>
          <div>

          </div>
          {context.states?.auth?.state.isAuthenticated
            ? <button className='btn btn-primary' type="submit">
              Submit
            </button>
            : <Link to="/login">
              <button type='button' className='btn btn-outline-primary'>
                Sigin to Comment
              </button>
            </Link>
          }
        </form>
      </div>
      <div>
      </div>
</article>
  
  )
}




export default ShowPost;

