import React, { useContext } from 'react';
import { Link, NavLink} from 'react-router-dom';
import { Nav, ButtonGroup,} from 'react-bootstrap';

import { SiteContext } from '../utils/context';

export default function NavMenu() {
  const context = useContext(SiteContext);
  //console.log(context)

  return (
    <header className="d-flex flex-wrap align-items-center justify-content-center justify-content-md-between py-2 mb-4 border-bottom">
      <Nav className=" card-maroon card-outline    shadow">
        <Link to="/" className="nav-link ">
          <span className="h4">Khalil <i className="bi bi-person-workspace fs-3 text-success"></i> Tuban</span>
        </Link>
      </Nav>

      <Nav className='  nav nav-pills nav-fill shadow border' role="tablist" variant="pills" defaultActiveKey='/'>
        <Nav.Item ><NavLink to="/" className="nav-link" >Home </NavLink></Nav.Item>
        <Nav.Item > <NavLink to="/portfolio" className="nav-link">Portfolio</NavLink></Nav.Item>
        <Nav.Item > <NavLink to="/blog/posts" className="nav-link">Blog</NavLink></Nav.Item>
        {/** 
        <NavDropdown title="Blog" id="nav-dropdown">
        <Nav.Item ><NavLink to="/blog/posts" className="dropdown-item" >Blog </NavLink></Nav.Item>
        <Nav.Item ><NavLink to="/blog/posts" className="dropdown-item" >posts </NavLink></Nav.Item>   
        </NavDropdown>
       */}
        <Nav.Item ><NavLink to="contact" className="nav-link ">Contact</NavLink></Nav.Item>

      </Nav>


      <ButtonGroup className=' shadow' >
        {!context.states?.auth?.state.isAuthenticated ?
          (<><NavLink className="btn btn-outline-primary" to='/signup'  >Sign up</NavLink> <NavLink className="btn btn-outline-success" to="/login"  > Login </NavLink> </>)
          : <button type='button' onClick={() => context.auth!.logout()} className="btn btn-outline-secondary"   > Logout </button>
        }
      </ButtonGroup>
    
    </header>

  );
}
