
import {useEffect, useId,useRef} from "react";
import moment from 'moment';
import { useSiteContext } from '../utils/context';

import { Link, Outlet} from 'react-router-dom';
import './blog.css';
import { IPost,ICategory } from '../types/blog';
import {ContinueReading} from  './posts';
import { Col} from 'react-bootstrap';
import MaroonCard from '../components/MaroonCard';

export default function Blog(props:any) {
  const context = useSiteContext()!;
  var featuredPosts = useRef<IPost[] >([]);
 useEffect(()=>{
!context.states?.category?.state.items && context.getSet!('categories');

 },[context.states?.category?.state.items,context.getSet])

 useEffect(()=>{
  if(context.states?.posts.state.items){
   featuredPosts.current = context.states?.posts.state.items!.filter(p => p.isfeatured).slice(0, 2);
}
 },[context.states?.posts.state.isLoaded])

 const RenderFeatured = (post: IPost) => (
 
  <div className="col-md-6" >
     <MaroonCard textSize="sm" title='Featured'>
    <div className="row g-0 border rounded overflow-hidden flex-md-row p-3  shadow-sm">
      <div className="col p-2 d-flex flex-column position-static">
        <h5>{post.title}</h5>
        <h3 className="mb-0">Featured post</h3>
        <div className="mb-1 text-muted">
          {moment(post.createdAt).format('MMMM Do, YYYY | h:mm a')}
          | By:  <Link to={"/profile/" + post.author?._id} state={post}>
            {post.author?.firstname! + ' ' + post.author?.lastname!}
          </Link>
        </div>
        <p className="card-text text-truncate">{post.descriptions}.</p>
        <ContinueReading {...post} />
      </div>

    </div>
    </MaroonCard>
  </div>
  
)
const RenderCategories= (props:any)=>{
  const Category= (cat:ICategory)=> <Link   to={'/blog/posts?category='+cat._id } state={{category:cat}} className="btn btn-outline-dark btn-sm m-1">{cat.name}</Link>
  return<>{
    context.states?.category?.state.items ?
    <div className="py-3 d-grid gap-2 d-md-block">
      <h4> Categores : <i className="bi bi-tags"></i></h4>
         {context.states?.category.state.items.map(c=> <Category  {...c} key={context.getId!()} />)}
        </div> :  context.loader!()
      }
     </> 
  
}
    return (
        <>
  <div className="p-4 p-md-5 mb-4 text-white rounded bg-dark">
    <div className="col-md-6 px-0">
      <h4 className="display-6 fst-italic">Blog Migrating here soon ..!</h4>
      <p className="lead mb-0"><Link to="/" className="text-white fw-bold">Continue reading...</Link></p>
    </div>
  </div>

{
    featuredPosts.current ?
  <div className="row mb-2">
  { featuredPosts.current!.map(p => <RenderFeatured {...p} key={p._id} />)}
  </div> : "" 
}
  <div className="row g-5">
    <div className="col-md-8">

      
      <Outlet />
    </div>

    <div className="col-md-4">
      <div className="position-sticky" style={{top:10}}>
        <div className="p-4 mb-3 bg-light rounded">
          <h4 className="fst-italic">About</h4>
          <p className="mb-0">Customize this section to tell your visitors a little bit about your publication, writers, content, or something else entirely. Totally up to you.</p>
        </div>
       <RenderCategories key={useId()}/>
        <div className="p-4">
          <h4 className="fst-italic">Archives</h4>
          <ol className="list-unstyled mb-0">
            <li><a href="/">March 2021</a></li>
            <li><a href="/">February 2021</a></li>
            <li><a href="/">January 2021</a></li>
            <li><a href="/">December 2020</a></li>
            <li><a href="/">November 2020</a></li>
            <li><a href="/">October 2020</a></li>
            <li><a href="/">September 2020</a></li>
            <li><a href="/">August 2020</a></li>
            <li><a href="/">July 2020</a></li>
            <li><a href="/">June 2020</a></li>
            <li><a href="/">May 2020</a></li>
            <li><a href="/">April 2020</a></li>
          </ol>
        </div>

        <div className="p-4">
          <h4 className="fst-italic">Elsewhere</h4>
          <ol className="list-unstyled">
            <li><a href="/">GitHub</a></li>
            <li><a href="/">Twitter</a></li>
            <li><a href="/">Facebook</a></li>
          </ol>
        </div>
      </div>
    </div>
  </div>
</>
    );
}
