
import React, { useState } from "react";
//import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
//import { faEye} from "@fortawesome/free-solid-svg-icons";
import { Link } from 'react-router-dom';

import BgImage from "../assets/img/illustrations/signin.svg";
import Profile3 from "../assets/img/team/profile-picture-3.jpg";

export const Lock= () => {
  const [showPassword, setShowPassword] = useState(false);
  const passwordInputType = showPassword ? "text" : "password";
  const passwordIconColor = showPassword ? "#262B40" : "";

  const togglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };

  return (
      <section className="vh-lg-100 bg-soft d-flex align-items-center my-4">
        <div className="container">
          <p className="text-center">
            <Link  to="/" className="card-link text-gray-700">
            <i className="bi bi-chevron-double-right"></i> Back to homepage
            </Link>
          </p>
          <div className="row justify-content-center form-bg-image" style={{ backgroundImage: `url(${BgImage})` }}>
            <div className="col-xs-12 d-flex align-items-center justify-content-center">
              <div className="bg-white shadow-soft border border-light rounded p-4 p-lg-5 w-100 fmxw-500">
                <div className="text-center text-md-center mb-4 mt-md-0">
                  <div className="user-avatar large-avatar mx-auto mb-3 border-dark p-2">
                    <img src={Profile3} className="rounded-circle" alt="pppp" />
                  </div>
                  <h3 className="mb-3">Bonnie Green</h3>
                  <p className="text-gray">Better to be safe than sorry.</p>
                </div>
                <form className="mt-5">
                  <div id="password" className="input-groud mb-4">
                    <label className="input-groud-text mb-4">Your Password</label>
                      <input className="form-control mb-4" required type={passwordInputType} placeholder="Password" />
                      <label className="input-groud-text" onClick={togglePasswordVisibility}>
                        {/*<FontAwesomeIcon color={passwordIconColor} icon={faEye} />*/}
                      </label>
                    </div>
                  <button  type="submit" className="btn btn-primary w-100">
                    Unlock
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
  );
};
