import React, { useContext, useState, useEffect } from 'react';
import {SiteContext } from '../utils/context';
import { Link } from 'react-router-dom';
import history from '../utils/history';

import { Button, Table } from 'react-bootstrap';


const ShowMessages = (props:any) => {
  const context = useContext(SiteContext)

  const [messages, setMessages] = useState<{}[]>([])

  useEffect(() => {
    const username = context.states?.auth.state.profile!.username
    context.ajax?.get('/api/get/usermessages', {params: {username: username}})
      .then(res =>  setMessages([...res.data]))
      .catch(function (error) {
          console.log(error);
        })
   }, [] )  //eslint-disable-line

   const RenderMessages = (props:any) => (
     <tr>
         <td>
           <p> <strong>From: </strong>  {props.message.message_sender} </p>
           <p> <strong>Title </strong>   { props.message.message_title } </p>
           <p><strong> Message:</strong>  { props.message.message_body } </p>
           <small> { props.message.date_created } </small>
           <br />
           <Link to={{pathname:"/replytomessage"}} state={props}>
               <button>
                  Reply
               </button>
            </Link>
           <Button onClick={() => DeleteMessage(props.message.mid)}> Delete </Button>
           <br />
           <br />
           <Button onClick={() => history.back()}> Cancel </Button>
        </td>
       </tr>
   )

  const DeleteMessage = (mid:any) => {
    context.ajax?.delete('/api/delete/usermessage', { data: { mid: mid }})
    .then(res => console.log(res))
    .catch(function (error) {
        console.log(error);
      })
     .then(() => setTimeout( function() { history.replace('/') }, 700))
  }



  return (
    <div>
      <div className='FlexRow'>
      <Table striped bordered hover>
      <thead>
    <tr>
      <th>#</th>
      <th>Messages</th>
    </tr>
  </thead>
  <tbody>
          { messages
            ? messages.map( (message:any) =>
              <RenderMessages key={message.mid} message={message} />
            )
            : null
          }
         </tbody>
        </Table>
      </div>
    </div>
  )
}



export default (ShowMessages);
