import React, {useEffect} from 'react'
import history from '../utils/history';
import { Link } from 'react-router-dom';
import { Row, Card, Col, Button,} from 'react-bootstrap';
import { GenericForm, GenericInput} from '../utils/forms';
import {useSiteContext } from '../utils/context';

export default function Login() {

const context = useSiteContext()

useEffect(()=> {
  if (context.states?.auth.state.isAuthenticated) {
    //console.log('auth states profile keys are: '+Object.keys(context.auth?.states!.profile!));
    history.back();
  }else{
    console.log(' authStates === null')
    if(context.states?.auth.state.loginErrors){
    console.log(' login errors are');
    console.log(context.states.auth?.state.loginErrors);
    }
  }
},[context.states?.auth?.state])


  return (
    <div className="hold-transition login-page ">
      <div className="login-box ">
        <div className="login-logo">
          <Link to="/login" className='nav-link' ><b>Login</b>Page</Link>
        </div>

        <Card>
          <Card.Body className="login-card-body">
            <p className="login-box-msg">Sign in to start your session</p>

            <GenericForm  serverErrors={context.states?.auth.state.loginErrors} cleandata={async (d:any)=> console.log(await context.auth!.login(d.email,d.password ))}>

             <GenericInput isfloating='false' as='input' name="email" type="email" lable='Email Address' placeholder="Email Address" required={true} icon="bi bi-envelope" />
              <GenericInput isfloating='false' as='input' name="password" lable='Password' placeholder="Password" type="password" required={true} icon="bi bi-lock" minLength={6} maxLength={11} />
              <Row>
                <Col sm={8} >
     <GenericInput isfloating='false' as='input' type="checkbox" required={false} name="remember" lable='Remember me' />
                </Col>

                <Col sm={4}>
                  <Button variant="primary" type="submit">Sign in </Button>
                </Col>

              </Row>
            </GenericForm >
            <div className="social-auth-links text-center mb-3">
              <p>- OR -</p>
              <Link to="/" className="btn d-block btn-primary mb-2">
                <i className="bi bi-facebook"></i> Sign in using Facebook

              </Link>
              <Link to="/" className="btn d-block btn-danger">
                <i className="bi bi-facebook"></i> Sign in using Google+
              </Link>
            </div>

            <p className="mb-1">
              <Link className='nav-link text-center ' to="/">I forgot my password</Link>
            </p>
            <p className="mb-0">
              <Link to="/signup" className="text-center nav-link">Create new account</Link>
            </p>
          </Card.Body>

        </Card>
      </div>
    </div>
  )
}
