import React, { useContext } from 'react';
import {SiteContext } from '../utils/context';
import history from '../utils/history';


import { Button} from 'react-bootstrap';
//import MaroonCard from '../components/MaroonCard';
//import { BoostrapInput } from '../utils/html5validation';


const ReplytoMessage = (props:any) => {
  const context = useContext(SiteContext)

  const handleSubmit = (event:any) => {
    event.preventDefault()
    const message_to_username = props.location.state.props.message.message_sender
    const message_from_username = context.states?.auth?.state.profile!.username
    const message_title = event.target.title.value
    const message_body = event.target.body.value

    const data = {message_sender: message_from_username,
                  message_to: message_to_username,
                  title: message_title,
                  body: message_body }
                  context.ajax?.post('/api/post/messagetodb', data)
      .then((response:any) => console.log(response))
      .catch(function (error:any) {
        console.log(error);
      })
      .then(()=> setTimeout( function() { history.replace('/') }, 700))
     }

    return (
      <div>
      <h2> Message: </h2>
      <div className='FlexColumn'>
        <div>
          <p><strong>{props.location.state.props.message.message_title}</strong></p>
        </div>
        <div>
          <p>{props.location.state.props.message.message_body}</p>
        </div>
        <div>
          <small> By: {props.location.state.props.message.message_sender}</small>
        </div>
      </div>

      <div className="FlexRow">
        <form onSubmit={handleSubmit}>
          <input
          aria-label='title'
          type='text'
            id="title"       
              />
          <br/>
            <textarea
            id="body"
            aria-label='body'
            rows={4}
          />
          <br/>
            <Button variant="primary" color="primary" type="submit" >Submit</Button>
        </form>
          <br />
        </div>
        <div className="FlexRow">
          <Button variant='info' className="CancelButton" onClick={() => history.replace('/')}> Cancel </Button>
        </div>
      </div>
  );
}



export default (ReplytoMessage);
