import { Row, Col } from 'react-bootstrap';
import { FormBuilder } from '../utils/forms';
import MaroonCard from '../components/MaroonCard';

export default function Test() {
 
  const elements = FormBuilder({ 
               
    elements: [
      {
        lable: "Select subject",
        element: {
            as:'select',
            name: "subject",
            required: true    
        },
        selectOptions: {
            lable: "Select subject",
            options: [
              { title: "consultation", value: "1" },
              { title: "Question", value: "2" },
              { title: "Hire me", value: "3" },
              { title: "Message", value: "4" }
            ]
          }
      },
      {
        lable: "Full Name",
        element: {
            as:'input',
            name: "fullname",
            type: "text",
            required: true,
            placeholder: "Your Name",
            pattern: '^[A-Za-z0-9 _]*[A-Za-z0-9][A-Za-z0-9 _]',
            minLength: 3,
            maxLength: 30
          }
      },
      {
        lable: "Email Address",
        element: {
            as:'input',
            type:'email',
            name: "email",
            required: true,
            placeholder: "Your email",
            minLength: 3,
            maxLength: 30
          }
        
      },
      {
        lable: "Telphone Number",
        element:{
            as:'input',
            name: "telphone",
            type: "tel",
            placeholder: "Your phone number",
            pattern: '[+0-9]{11,15}',
          }
        
      },
      {
        lable: "Your Message",
        element: {
            name: "message",
            required: true,
            placeholder: "Your message",
            as:"textarea",
            minLength: 3,
            style: { height: '100px' }
          }                   
      },
      {
        lable: "Shall we call you .?",
        element: {
            as:'input',
            name: "terms",
            type: "checkbox",
            required: true,
          }
      }
    ],
    isfloating:true,
    form: {
      attrs:{
        id: "contactForm",
        name: "contactForm",
      },
      cleandata: (d: any) => alert(d)
    },
    addSubmit:true
  });

  return (

    <Row className="justify-content-md-center">
      <Col md="6" className="mx-auto">
        <MaroonCard title='Contact'>
        <h4 className='text-center'>Please use this form</h4>

          <hr className='py-2 bg-warning'/>
            {elements}     
        </MaroonCard>
      </Col>
    </Row>

  );
}