import React, {Component} from "react";

// react-bootstrap components
import {
  Button,
  Card,
  FormGroup,
  ButtonGroup,
  FormControl,
  FormCheck,
  Form,
  Container,
  Row,
  Col,
} from "react-bootstrap";
//import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
//import { faAngleLeft, faEnvelope, faUnlockAlt } from "@fortawesome/free-solid-svg-icons";
//import { faFacebookF, faGithub, faTwitter } from "@fortawesome/free-brands-svg-icons";
import { Link } from 'react-router-dom';

import ImgOne from "../assets/img/photo-1431578500526-4d9613015464.jpeg";
import face3 from "../assets/img/faces/face-3.jpg";

export default function UserProfile(){

  return (
      <Container fluid>
        <Row>
          <Col md="8">
            <Card>
              <Card.Header>
                <Card.Title as="h4">Edit Profile</Card.Title>
              </Card.Header>
              <Card.Body>
                <Form>
                  <Row>
                    <Col className="pr-1" md="5">
                      <FormGroup>
                        <FormCheck.Label>Company (disabled)</FormCheck.Label>
                        <FormControl
                          defaultValue="Creative Code Inc."
                          disabled
                          placeholder="Company"
                          type="text"
                        />
                      </FormGroup>
                    </Col>
                    <Col className="px-1" md="3">
                      <FormGroup>
                        <label>Username</label>
                        <FormControl defaultValue="michael23" placeholder="Username" type="text"/>
                      </FormGroup>
                    </Col>
                    <Col className="pl-1" md="4">
                      <FormGroup>
                        <label htmlFor="exampleInputEmail1">
                          Email address
                        </label>
                        <FormControl
                          placeholder="Email"
                          type="email"
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col className="pr-1" md="6">
                      <FormGroup>
                        <label>First Name</label>
                        <FormControl
                          defaultValue="Mike"
                          placeholder="Company"
                          type="text"
                        />
                      </FormGroup>
                    </Col>
                    <Col className="pl-1" md="6">
                      <FormGroup>
                        <label>Last Name</label>
                        <FormControl
                          defaultValue="Andrew"
                          placeholder="Last Name"
                          type="text"
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col md="12">
                      <FormGroup>
                        <label>Address</label>
                        <FormControl
                          defaultValue="Bld Mihail Kogalniceanu, nr. 8 Bl 1, Sc 1, Ap 09"
                          placeholder="Home Address"
                          type="text"
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col className="pr-1" md="4">
                      <FormGroup>
                        <label>City</label>
                        <FormControl
                          defaultValue="Mike"
                          placeholder="City"
                          type="text"
                        />
                      </FormGroup>
                    </Col>
                    <Col className="px-1" md="4">
                      <FormGroup>
                        <label>Country</label>
                        <FormControl
                          defaultValue="Andrew"
                          placeholder="Country"
                          type="text"
                        />
                      </FormGroup>
                    </Col>
                    <Col className="pl-1" md="4">
                      <FormGroup>
                        <label>Postal Code</label>
                        <FormControl
                          placeholder="ZIP Code"
                          type="number"
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col md="12">
                      <FormGroup>
                        <label>About Me</label>
                        <FormControl
                        
                          defaultValue="Lamborghini Mercy, Your chick she so thirsty, I'm in
                          that two seat Lambo."
                          placeholder="Here can be your description"
                          as="textarea"
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Button
                    className="btn-fill pull-right"
                    type="submit"
                    variant="info"
                  >
                    Update Profile
                  </Button>
                  <div className="clearfix"></div>
                </Form>
              </Card.Body>
            </Card>
          </Col>
          <Col md="4">
            <Card className="card-user">
              <Card.Img  alt="..." src={ImgOne} className="card-image img-fluid"/>
              <Card.Body>
                <div className="m-auto text-center" >
                  <a href="/" onClick={(e) => e.preventDefault()}>
                    <Card.Img  
                      alt="..."
                      className="img-fluid"
                      height={150}
                      width={100}
                      src={face3} 
                      
                    />
                    <h5 className="title">Mike Andrew</h5>
                  </a>
                  <p className="description">michael24</p>
                </div>
                <p className="description text-center">
                  "Lamborghini Mercy <br></br>
                  Your chick she so thirsty <br></br>
                  I'm in that two seat Lambo"
                </p>
              </Card.Body>
              <hr></hr>
              <ButtonGroup className="mr-auto ml-auto">
                <Button
                  href="/"
                   onClick={(e) => e.preventDefault()}
                   color="primary"
                   size="sm"
                >
                   {/* <FontAwesomeIcon icon={faFacebookF} />*/}
                  
                </Button>
                <Button
                  href="/"
                   onClick={(e) => e.preventDefault()}
                   color="success"
                   size="sm"
                >
                   {/* <FontAwesomeIcon icon={faTwitter} />*/}
                  
                </Button>
                <Button
                 href="/"
                  onClick={(e) => e.preventDefault()}
                  color="dark"
                  variant="outline"
                  size="sm"
                >
                  
                   {/* <FontAwesomeIcon icon={faGithub} />*/}
                </Button>
              </ButtonGroup>
            </Card>
          </Col>
        </Row>
      </Container>
  );
};