import React from 'react';

import twiter from "../assets/icons/icons8-twitter-circled.gif"; 
import instgram from "../assets/icons/icons8-instagram.gif";
import linkden from "../assets/icons/icons8-linkedin-circled.gif"; 
import github from "../assets/icons/icons8-github.gif"


export default function Social(){
  //static displayName = Home.name;

    return (
    <div className="socials">
      <a href="/" target="_blank"
        ><img
          src={twiter}
          alt="Twitter"
          className="socicon"
      /></a>
      <a href="/" target="_blank"
        ><img
          src={instgram}
          alt="Instagram"
          className="socicon"
      /></a>
      <a href="https://www.linkedin.com/in/tuban" rel='noreferrer noopener'  target="_blank"
        ><img
          src={linkden}
          alt="Linkedin"
          className="socicon"
      /></a>
      <a href="https://github.com/Khalillof" rel='noreferrer noopener' target="_blank"
        ><img src={github} alt="Github" className="socicon"
      /></a>
    </div>
    );
}
