import {useReducer} from 'react';
import * as ACTION_TYPES from '../actions/action_types'
import * as ACTIONS from '../actions/actions';

export function defaultReducer<P>(state: P, action:  ACTIONS.IActionType<P>){
  const result = { ...state, ...action.payload };
  switch (action.type) {
      case ACTION_TYPES.LOGIN_SUCCESS:
        let  s_payload = JSON.stringify(action.payload);
        sessionStorage.setItem("auth",s_payload);
          return result;
      case ACTION_TYPES.LOGIN_FAILURE:
          sessionStorage.clear();
          return result;
      case ACTION_TYPES.LOGOUT:
          sessionStorage.clear();
          return result
      case ACTION_TYPES.SET_ITEMS:
          return result;
      case ACTION_TYPES.REMOVE_ITEMS:
          return result;
      case ACTION_TYPES.FETCH_REQUEST:
            return result;
      case ACTION_TYPES.HAS_ERRORR:
            return result
      case ACTION_TYPES.USER_INPUT_CHANGE:
          return result;
      case ACTION_TYPES.USER_INPUT_SUBMIT:
          return result;
      default:
          throw new Error('ACTION TYPE NOT FOUND');
  }
}
export function sortActions<P>(action: ACTIONS.IActionType<P>) {
    switch (action.type) {
      case ACTION_TYPES.FETCH_REQUEST:
        return ACTIONS.set_isLoading()
      case ACTION_TYPES.SET_ITEMS:
        return ACTIONS.set_items(action.payload)
      case ACTION_TYPES.REMOVE_ITEMS:
        return ACTIONS.remove_items()
      case ACTION_TYPES.HAS_ERRORR:
        return ACTIONS.set_hasError()
      default:
        throw new Error('ACTION TYPE NOT FOUND');
    }
  }
export interface IReducer<P>{
       (state:P,action:ACTIONS.IActionType<P>):P
}
export interface ISetReducer<P>{
    (initialState?:P |any):[P, React.Dispatch<ACTIONS.IActionType<P>>]
 // (reducer:IReducer<P>, initialState?:P |any):any
}

export function SetReducer<P>(initialState?:P |any){
  const reducer:IReducer<P>  = defaultReducer;
  return useReducer(reducer,initialState);
  }


