import React from "react";

type Rainbow =
    | "red"
    | "orange"
    | "yellow"
    | "green"
    | "blue"
    | "indigo"
    | "violet";

type AsProp<C extends React.ElementType> = {
    as?: C;
};

type PropsToOmit<C extends React.ElementType, P> = keyof (AsProp<C> & P);

// This is the first reusable type utility we built
type PolymorphicComponentProp<
    C extends React.ElementType,
    Props = {}
    > = React.PropsWithChildren<Props & AsProp<C>> &
    Omit<React.ComponentPropsWithoutRef<C>, PropsToOmit<C, Props>>;

// This is a new type utitlity with ref!
type PolymorphicComponentPropWithRef<
    C extends React.ElementType,
    Props = {}
    > = PolymorphicComponentProp<C, Props> & { ref?: PolymorphicRef<C> };

// This is the type for the "ref" only
type PolymorphicRef<C extends React.ElementType> =
    React.ComponentPropsWithRef<C>["ref"];

/**
* This is the updated component props using PolymorphicComponentPropWithRef
*/
export type TextProps<C extends React.ElementType> =
    PolymorphicComponentPropWithRef<
        C,
        { color?: Rainbow | "black", isfloating?:string, lable?:string, icon?:string }
    >;

/**
* This is the type used in the type annotation for the component
*/
export type TextComponent = <C extends React.ElementType = "span">(
    props: TextProps<C>
) => React.ReactElement | null;


// main components ===========================
export const Polymorphic: TextComponent = React.forwardRef(
    <C extends React.ElementType = "span">(
        props: TextProps<C>,
        ref?: PolymorphicRef<C>
    ) => {
        const Component = props.as || "span";
       
        const style = props.color ? { style: { color:props.color } } : {};

        return (
            <Component {...style}  {...props} ref={ref}>
                {props.children}
            </Component>
        );
    }
);
const getRandom = (num = 1) => window.crypto.getRandomValues(new Uint32Array(num)).toString();

export const GenericInput:TextComponent = React.forwardRef(
    <C extends React.ElementType = "input">(
        props: TextProps<C>,
        ref?: PolymorphicRef<C>
    ) => {
        const Component = props.as || "input";      
        const style = props.color ? { style: { color:props.color } } : {};


        var id = props.id ? props.id : getRandom();
        let tpx :any = props;
        const _type = tpx.type ?? "nulll";
        const isFloating = props.isfloating === 'true';
        const isRadioOrCheckbox = 'checkbox radio'.indexOf(_type) === 0;
        const varient = isRadioOrCheckbox === true ? 'form-check mb-3' : isFloating ? 'form-floating mb-3': 'input-group mb-3';
        let class_name = props.className ? props.className:  isRadioOrCheckbox === true ? 'form-check-input' : props.as === 'select' ? 'form-select' : 'form-control';
         

        return (
            <div className={varient} >
            <Component {...style}  {...props} ref={ref} id={id} className={class_name}>
                {props.children}
            </Component>
            {!isFloating && props.icon && <span className="input-group-text" id={id}><i className={props.icon}></i> </span>}
            {isFloating || isRadioOrCheckbox ?  <label htmlFor={id}>{props.lable}</label> : ""}
            </div>
        );
    }
);
