
import React from "react";
import { Link, NavLink } from 'react-router-dom';


export default function Footer() {

    return (
  <footer className="d-flex flex-wrap align-items-center justify-content-center justify-content-md-between py-3 my-3">
   
    <ul className="nav  rounded-5 p-1 shadow-sm">
    <li className='nav-item'><NavLink to="/hireme" className="nav-link rounded-5">Hire me</NavLink> </li>
    <li className='nav-item'><NavLink to="/contact" className="nav-link rounded-5">Contact</NavLink> </li>
    </ul>

<ul className="nav   rounded-5 shadow-sm">
      <li className='nav-item '><a title="facebook" href="/"  className="nav-link rounded-5" ><i className="bi bi-facebook text-primary fs-3 "></i></a></li>
      <li className='nav-item'> <a title="twitter" href="/"  className="nav-link rounded-5 "><i className="bi bi-twitter text-success fs-3"></i></a></li>
      <li className='nav-item'><a title="linkedin" href="/" className="nav-link rounded-5"><i className="bi bi-linkedin text-primary fs-3"></i></a></li>
      <li className='nav-item'><a title="github" href="/" className="nav-link rounded-5"><i className="bi bi-github text-secondary fs-3 "></i></a></li>
    </ul>

    <nav className=" card-maroon card-outline ">
    <Link to="/" className="nav-link rounded-5">
    <small className="text-muted"> 2021 {new Date().getFullYear()},  Built with &#x2661; by </small> 
    <p className="h6 text-center">Khalil <i className="bi bi-person-workspace fs-3 text-success"></i> Tuban</p>
    </Link>
    </nav>


   
  </footer>
    );
}
