import { useEffect, useState , useMemo} from 'react';
import { Link, useLocation} from "react-router-dom";

import moment from 'moment';
import { useSiteContext } from '../utils/context';

import { Col} from 'react-bootstrap';
import MaroonCard from '../components/MaroonCard';
import { GenericInput} from '../utils/forms';
//import Blog from './blog'
import { IPost,ICategory } from '../types/blog';
import './blog.css';

function useQuery(location:Location) {
  const { search } = location;
  return useMemo(() => new URLSearchParams(search), [search]);
}
export const PostLink = (props: { post: IPost, children?: any }) => (
  <Link to={'/blog/post/' + props.post._id} state={{ post: props.post }}> {props.children ?? props.post.title}
  </Link>
)
export  const ContinueReading = (post: IPost) => (<PostLink post={post}> Continue reading... !</PostLink>)

export const Posts = (props: any) => {
  const context = useSiteContext()!;
  const location: any = useLocation();
  const query = useQuery(location);
  //const { cid } = useParams();

var query_id =query.get('category');

function sortOut(posts:IPost[]){

  const indexOfLastPost = 1 * stateLocal.posts_per_page
  const indexOfFirstPost = indexOfLastPost - stateLocal.posts_per_page
  const last_page = Math.ceil(posts!.length / stateLocal.posts_per_page)

  setState({
    ...stateLocal,
    fetched: true,
    num_posts: posts.length,
    max_page: last_page,
    posts_slice: posts.slice(indexOfFirstPost, indexOfLastPost)
  })
}

  const [stateLocal, setState] = useState<{
    fetched: boolean,
    first_page_load: boolean,
    pages_slice: number[],
    max_page: number,
    items_per_page: number,
    opacity: number,
    currentPage: number,
    num_posts: number,
    posts_slice: any,
    posts_search: any[],
    posts_per_page: number
  }>({
    fetched: false,
    first_page_load: false,
    pages_slice: [1, 2, 3, 4, 5],
    max_page: 1,
    items_per_page: 3,
    opacity: 1,
    currentPage: 1,
    num_posts: 1,
    posts_slice: null,
    posts_search: [],
    posts_per_page: 3
  }
  );

 const [categoryNav, setIsCategory] = useState<{isCategoryNav:boolean,category?:ICategory,categoryId?:string}>({
  isCategoryNav:false,
 })
  useEffect(() => {

    if(query_id){
      //sortOut(context.states?.categoryNav.state.items!,true)
      context.getSet!('posts',{"category":query.get('category')!});
      let cat = null;
      if(location.state && Object.hasOwn(location.state, 'category')){
       cat = location.state.category;
      }else if(context.states?.category.state.items){
        cat = context.states?.category.state.items.find(c=> c._id! = query_id!)
      }
      setIsCategory({isCategoryNav:true,category:cat, categoryId:query_id})

    }else{
      categoryNav &&  setIsCategory({isCategoryNav:false})
      context.getSet!('posts'); 
    }
  }, [query_id])

  useEffect(() => {

    if (context.states?.posts.state.items) {
      sortOut(context.states?.posts.state.items)
    }
  }, [context.states?.posts.state.items])


  useEffect(() => {

    let page = stateLocal.currentPage
    let indexOfLastPost = page * 3;
    let indexOfFirstPost = indexOfLastPost - 3;
   let posts=context.states?.posts.state.items ?? [];
    setState({ ...stateLocal, posts_slice: posts.slice(indexOfFirstPost, indexOfLastPost) })
  }, [stateLocal.currentPage]) //eslint-disable-line




  const add_search_posts_to_state = (posts: any) => {
    setState({ ...stateLocal, posts_search: [] });
    setState({ ...stateLocal, posts_search: [...posts] });
  }


  const handleSearch = (event: any) => {
    setState({ ...stateLocal, posts_search: [] });
    const search_query = event.target.value
    context.ajax!.get!('posts/', { search_query: search_query })
      .then((res: any) => res.data.length !== 0
        ? add_search_posts_to_state(res.data)
        : null)
      .catch(function (error: any) {
        console.log(error);
      })
  }

  const RenderPosts = (post: IPost) => (
    <article className='blog-post'>
      <h3 className="blog-post-title pb-3 mb-3 fst-italic">
        <Link to={'/blog/post/' + post._id} state={{ post: post }}> {post.title}
        </Link>
      </h3>
      <p className="blog-post-meta">{moment(post.createdAt).format('MMMM Do, YYYY | h:mm a')}  <i className="bi bi-hand-thumbs-up text-success"></i> likes <strong>{post.likes} </strong>
        | By:  <Link to={"/profile/" + post.author?._id} state={post}>
          {post.author?.firstname! + ' ' + post.author?.lastname!}
        </Link>
      </p>

      <p className='lead'> {post.descriptions}.  <ContinueReading {...post} /></p>
    </article>
  )
  

  const CategoryNavRender = (cat: ICategory) => (<h6 className='text-secondary'> serach result for category: {cat.name} counts: ( {context.states?.posts.state.items?.length.toString()} )</h6>)

  
  const page_change = (page: any) => {
    window.scrollTo({ top: 0, left: 0, behavior: 'smooth' })

    //variables for page change
    let next_page = page + 1
    let prev_page = page - 1

    //handles general page change
    //if(state.max_page < 5 return null)
    if (page > 2 && page < stateLocal.max_page - 1) {
      setState({
        ...stateLocal,
        currentPage: page,
        pages_slice: [prev_page - 1,
          prev_page,
          page,
          next_page,
        next_page + 1],
      })
    }
    if (page === 2) {
      setState({
        ...stateLocal,
        currentPage: page,
        pages_slice: [prev_page,
          page,
          next_page,
          next_page + 1,
          next_page + 2],
      })
    }
    //handles use case for user to go back to first page from another page
    if (page === 1) {
      setState({
        ...stateLocal,
        currentPage: page,
        pages_slice: [page,
          next_page,
          next_page + 1,
          next_page + 2,
          next_page + 3],
      })
    }
    //handles last page change
    if (page === stateLocal.max_page) {
      setState({
        ...stateLocal,
        currentPage: page,
        pages_slice: [prev_page - 3,
        prev_page - 2,
        prev_page - 1,
          prev_page,
          page],
      })
    }
    if (page === stateLocal.max_page - 1) {
      setState({
        ...stateLocal,
        currentPage: page,
        pages_slice: [prev_page - 2,
        prev_page - 1,
          prev_page,
          page,
          next_page],
      })
    }
  }

  return (
 
    <MaroonCard title='Posts'>
      {/*
    <Nav style={{opacity: stateLocal.opacity, transition: 'opacity 2s ease'}}>
    { context.auth?.states.isAuthenticated
      ?  <Link to="/blog/addpost" className='btn btn-outline-danger'>
              Add Post
          </Link>
      : <Link to="/login" className='btn btn-outline-danger'>
              Sign in to Add Post
          </Link>
        }
    </Nav>
    <br />
      */}

      <Col md="6" className="mx-auto">
        <GenericInput isfloating='false' as='input' name="password" lable='seearch' placeholder="search..." type="search" required={true} icon="bi bi-search" minLength={3} maxLength={50} onChange={handleSearch} />
      </Col>

      {stateLocal.posts_search && stateLocal.posts_search.map((post: IPost) => <RenderPosts key={post._id!} {...post} />)}

     
      <h1 className='display-6'>Posts</h1>
 {categoryNav.isCategoryNav ? <CategoryNavRender {...categoryNav.category!}/> : ""}
  {context.states?.posts.state.isLoaded && stateLocal.posts_slice ? stateLocal.posts_slice.map((post: any) => <RenderPosts key={post._id} {...post} />) : context.loader!()}
{/*<button type='button' className="btn btn-primary" onClick={()=> console.log(context.states?.posts.state)}>Log Context</button>*/}
<br/>
      <div className="btn-toolbar" role="toolbar" aria-label="Toolbar with button groups">

        <div className="btn-group" role="group" aria-label="First group">
          <button type="button" className="btn btn-outline-primary" onClick={() => page_change(1)}>First</button>
          <button type="button" className="btn btn-outline-primary" onClick={() => page_change(stateLocal.currentPage - 1)} >
            <i className="bi bi-chevron-double-left"></i> Prev
          </button>
        </div>

        <div className="btn-group me-2" role="group" aria-label="Second group">
          {stateLocal.pages_slice.map((page) =>
            <button type='button'
              onClick={() => page_change(page)}
              className={stateLocal.currentPage === page
                ? "btn btn-primary"
                : "btn btn-outline-secondary"}
              key={page}>
              {page}
            </button>
          )}
        </div>

        <div className="btn-group" role="group" aria-label="Third group">
          <button type="button" className="btn btn-outline-primary" onClick={() => page_change(stateLocal.currentPage + 1)}>
            Next <i className="bi bi-chevron-double-right"></i>
          </button>
          <button type="button" className="btn btn-outline-primary" onClick={() => page_change(stateLocal.max_page)} >Last</button>

        </div>

      </div>
    </MaroonCard>

  )
}


