import React, { useContext, useState, useEffect } from 'react';
import {SiteContext } from '../utils/context';

import { Link } from 'react-router-dom';
import history from '../utils/history';
import { Card,Button, Modal } from 'react-bootstrap';
//import MaroonCard from '../components/MaroonCard';



const Profile = () => {
  const context = useContext(SiteContext)

  const [stateLocal, setState] = useState({
    open: false,
    post_id: null,
    posts: []
  })

  useEffect(() => {
    const user_id = context.states?.auth?.state.profile!._id
    context.ajax?.get('/api/get/userposts', { params: { user_id: user_id } })
      .then((res:any) => setState({ ...stateLocal, posts: [...res.data] as never }))
      .catch((err:any) => console.log(err))
  })

  const handleClickOpen = (pid:any) => {
    setState({ open: true, post_id: pid,posts: [] as never })
  }

  const handleClickClose = () => {
    setState({ open: false, post_id: null, posts: [] as never })
  }

  const DeletePost = () => {
    const post_id = stateLocal.post_id
    context.ajax?.delete('api/delete/postcomments', { data: { post_id: post_id } })
      .then(() => context.ajax?.delete('/api/delete/post', { data: { post_id: post_id } })
        .then((res:any) => console.log(res)))
      .catch(err => console.log(err))
      .then(() => handleClickClose())
      .then(() => setTimeout(() => history.replace('/'), 700))
  }

  const RenderProfile = (props:any) => {
    return (
      <div>
        <h1>{props.profile.profile.nickname}</h1>
        <br />
        <img src={props.profile.profile.picture} alt="" />
        <br />
        <h4> {props.profile.profile.email}</h4>
        <br />
        <h5> {props.profile.profile.name} </h5>
        <br />
        <h6> Email Verified: </h6>
        {props.profile.profile.email_verified ? <p>Yes</p> : <p>No</p>}
        <br />
      </div>

    )
  }

  const RenderPosts = (post:any) => (
    <Card style={{ width: '500px', height: '200px', marginBottom: '10px', paddingBottom: '80px' }}>
      <Card.Header
      /*
        title={<Link to={'/post/' + post.post.pid} state={ post } >
          {post.post.title}
        </Link>
      
        subheader={
          <div className="FlexColumn">
            <div className="FlexRow">
              {post.post.date_created}
            </div>
            <div className="FlexRow">
              <Link to={'/editpost/' + post.post.pid} state={ post }>
                <button>
                  Edit
                </button>
              </Link>
              <button onClick={() => handleClickOpen(post.post.pid)}>
                Delete
              </button>
            </div>
          </div>
        }  
        */
      />
      <br />
      <Card.Body>
        <Card.Text style={{ overflow: 'hidden' }}> {post.post.body} </Card.Text>
      </Card.Body>

    </Card>
  );


  return (
    <>
      <div>
        <Link to={{ pathname: "/showmessages/" + context.states?.auth.state.profile!._id }}>
          <Button variant="contained" color="primary" type="submit">
            Show Messages
          </Button>
        </Link>
        <RenderProfile profile={context.states?.auth?.state.profile} />
      </div>
      <div>
        {stateLocal.posts
          ? stateLocal.posts.map((post:any) =>
            <RenderPosts post={post} key={post.pid} />)
          : null}
      </div>
      <Modal
        open={stateLocal.open}
        onClose={handleClickClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <Modal.Header closeButton>
          <Modal.Title id="alert-dialog-title"> Confirm Delete? </Modal.Title>
        </Modal.Header>

        <Modal.Body>
          <p
            id="alert-dialog-description"
          >
            Deleteing Post
          </p>
          <Modal.Footer>
            <Button variant="primary" onClick={() => DeletePost()}>
              Agree
            </Button>
            <Button variant="secondary" onClick={() => handleClickClose()}>
              Cancel
            </Button>
          </Modal.Footer>
        </Modal.Body>
      </Modal>

    </>
  )
}



export default (Profile);
