import React, { useContext} from 'react';
import history from './history';
import * as ACTIONS from '../store/actions/actions';
import { getRandom } from './html5validation'
import {
  ISiteContextProps,
  IStateSetter,
  IReducers,
  IUseReducers,
} from '../types/utils'

import { SetReducer,sortActions  } from '../store/reducers/reducers';
import AppRoutes from './routes';


function StateSetter<P>(red: [P, React.Dispatch<ACTIONS.IActionType<P>>]): IStateSetter<P> {
  return { state:red[0], dispatch: red[1] }
}


class useReducers implements IUseReducers {
  constructor( rds:IReducers) {
 
    this.auth = StateSetter(rds.auth)
    this.posts = StateSetter(rds.posts);
    this.category = StateSetter(rds.category);
    this.form = StateSetter(rds.form);
  }
  auth: IStateSetter<ACTIONS.IAuthPayload>
  posts: IStateSetter<ACTIONS.IPostPayload>
  category: IStateSetter<ACTIONS.ICategoryPayload>
  form: IStateSetter<ACTIONS.IFormPayload>

  Set={
    IsLoading: (name: string) => this.addToDispatch(name, ACTIONS.set_isLoading()),
    HasError: (name: string) => this.addToDispatch(name, ACTIONS.set_hasError()),
    Items: (name: string, items: any[]) => this.addToDispatch(name, ACTIONS.set_items({ items, isLoaded: true })),
    removeItems: (name: string) => this.addToDispatch(name, ACTIONS.remove_items()) 
  }
  useContextSubmit(event: any) {
    event.preventDefault();
    event.persist();
    this.form.dispatch(ACTIONS.user_input_submit(event.target.useContext.value))
  }
  useContextChange(event: any) { this.form.dispatch(ACTIONS.user_input_change(event.target.value)) }

  addToDispatch<P>(dispatchName: string, actionType: ACTIONS.IActionType<P>) {

    switch (dispatchName) {
      case 'posts':
        return this.posts.dispatch(sortActions<ACTIONS.IPostPayload>({ type: actionType.type, payload: ACTIONS.postPayloadInit(actionType.payload) }))
      case 'categories':
        return this.category.dispatch(sortActions<ACTIONS.ICategoryPayload>({ type: actionType.type, payload: ACTIONS.categoryPayloadInit(actionType.payload) }))
     case 'auth':
        return this.auth.dispatch(sortActions<ACTIONS.IAuthPayload>({ type: actionType.type, payload: ACTIONS.authPayloadInit(actionType.payload) }))
      default:
        throw new Error('ACTION TYPE NOT FOUND');
    }
  }
}


class TestSiteContextProps implements ISiteContextProps {

  constructor(reducers:IReducers) {
    this.states = new useReducers(reducers);
  }
  baseUrl = 'https://khaliltuban.co.uk';
  states: IUseReducers
  getId = getRandom;
  loader() {
    return <div className="d-flex justify-content-center mb-3">
      <div className="spinner-border text-primary" role="status">
        <span className="visually-hidden">Loading...</span>
      </div>
    </div>
  }

  getSet(name: string, query?: Record<string, string>, callback?:Function) {
    this.states.Set?.IsLoading(name);
    this.ajax!.get!(name, query)
      .then((res: any) => {
        if(res.items)
        callback ? callback(res.items) : this.states.Set?.Items!(name, res.items)
      }).catch((err: any) => {
        this.states.Set?.HasError(name)
        console.log(err)
      })
  }

  ajax = {

    setUrl: (path?: string, querys?: Record<string, any>): URL => {
      let url = new URL(path!, 'http://localhost:8000');
      if (querys)
        for (let k in querys) {
          url.searchParams.append(k, querys[k]);
        }
      return url;
    },
    init: async (method: string, url?: URL, data?: any) => {

      const body = data && JSON.stringify(data) // body data type must match "Content-Type" header
      const response = await fetch(url?.href!, {
        method: method, // *GET, POST, PUT, DELETE, etc.
        mode: 'cors', // no-cors, cors, same-origin
        //credentials: 'same-origin', // include, *same-origin, omit
        headers: (() => {
          let headers: Record<string, string> = {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': 'https://khaliltuban.co.uk',
          }
          if (this.states.auth.state.isAuthenticated && this.states.auth.state.tokens) {
            headers['Authorization'] = `Bearer ${this.states.auth.state.tokens.accessToken}`
          }
          return headers;
        })(),
        // redirect: 'follow', // manual, *follow, error
        referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
        body
      });
      if (response.ok)
        return await response.json();
      if (response.json)
        return await response.json();
      else
        return response;
      // parses JSON response into native JavaScript objects
    },

    get: async (path?: string, querys?: Record<string, any>) => await this.ajax.init('GET', this.ajax.setUrl(path!, querys))
    ,
    post: async (path: string, data: any, querys?: Record<string, any>) => await this.ajax.init('POST', this.ajax.setUrl(path!, querys!), data)
    ,
    put: async (path: string, data: any, querys?: Record<string, any>) => await this.ajax.init('PUT', this.ajax.setUrl(path!, querys!), data)
    ,
    delete: async (path: string, data: any, querys?: Record<string, any>) => await this.ajax.init('PUT', this.ajax.setUrl(path!, querys!), data)
  }

  auth = {
    getAccessToken: (): ACTIONS.ITokens | null => {
      return this.states.auth.state.isAuthenticated ? this.auth.getItem('auth')['tokens'] : null;
    },
    getItem: (name: string) => {
      if (name) {
        let item = sessionStorage.getItem(name);
        return item ? JSON.parse(item) : null;
      } else {
        return null;
      }
    },
    saveItem: (name: string, item: any) => {
      item = JSON.stringify(item)
      if (item) {
        sessionStorage.setItem(name, item);
      }
    },
    clearItems: () => {
      sessionStorage.clear()
    },
    isAuthenticated: () => {
      let tokens = this.auth.getAccessToken();

      return tokens && tokens.accessTokenExpireAt ? new Date().getTime() < tokens!.accessTokenExpireAt : false;
    },
    setProfile: async (email: string): Promise<any> => {
      try {
        if (email) {
          let responce: any = await this.ajax.get('auth/profile/', { email: email! });
          //console.log(responce);

          if (responce && responce.success) {
            this.auth.saveItem('profile', JSON.stringify(responce as string))
            return true;
          }
          return false;
        } else {
          return false;
        }
      } catch (err: any) {
        console.log(err);
        throw err;
      };
    },
    login: async (email: string, password: string) => {
      this.ajax.post('auth/signin', { email, password, username: 'tuban' }).then(data => {
        if (data.success && data.user) {
          this.states.auth.dispatch(ACTIONS.login_success({
            isAuthenticated: true,
            tokens: data.tokens,
            profile: data.user,
            loginErrors: null
          }));

          return true;
        } else {
          this.auth.loginFailur(data.errors);
          return false;
        }
      }).catch((err: any) => {
        this.auth.loginFailur(err);
        console.log(err);
        throw err;
      });
    },
    loginFailur: (errors: any) => this.states.auth.dispatch(ACTIONS.login_failure(errors)),
    logout: () => {
      this.states.auth.dispatch(ACTIONS.logout());
      history.replace('/')
    }
  }

}

export const SiteContext = React.createContext<ISiteContextProps>({});


export const ContextState = (props: ISiteContextProps) => {

  const rds:IReducers = {
            auth:SetReducer<ACTIONS.IAuthPayload>(ACTIONS.authPayloadInit()),
            posts:  SetReducer<ACTIONS.IPostPayload>(ACTIONS.postPayloadInit()),
            category: SetReducer<ACTIONS.ICategoryPayload>(ACTIONS.categoryPayloadInit()),
            form: SetReducer<ACTIONS.IFormPayload>(ACTIONS.formPayloadInit())
}

  const values = new TestSiteContextProps(rds);

  return (
    <SiteContext.Provider
      value={values}>
      <AppRoutes />
    </SiteContext.Provider>
  )
}

export function useSiteContext() {
  return useContext(SiteContext);
}

