//import React, { Component } from 'react';
import history from '../utils/history';
import { Link } from 'react-router-dom';
//import BgImage from "../assets/img/illustrations/signin.svg";
import { Row, Col , Button} from 'react-bootstrap';
import MaroonCard from '../components/MaroonCard';
import { FormBuilder } from '../utils/forms';

export default function Signup () {

  
  const elements =FormBuilder({
    elements: [
      {
        lable: "Email Address",
        element: {
           as:'input',
            name: "email",
            type: "email",
            required: true,
            placeholder: "Your Email",
            minLength: 3,
            maxLength: 20
        }
      },
      {
        lable: "Password",
        element: {
          as:'input',
            name: "password",
            type:'password',
            required: true,
            placeholder: "Enter password",
            minLength: 6,
            maxLength:11,
        }
      },
      {
        lable: "Confirm Password",
        element: {
          as:'input',
            name: "confirmpassword",
            type:'password',
            required: true,
            placeholder: "Confirm password",
            minLength: 6,
            maxLength:11,
        }
      },
      {
        lable: "agree to terms & conditions",
        element: {
            as:'input',
            name: "terms",
            type: "checkbox",
            required: true,
          }
      }
    ],
    isfloating:true,
    form: {
      attrs:{
        id: "signupForm",
        name: "signupForm",
      },
      cleandata: (d: any) => alert(d)
    },
    addSubmit:true,
    extra: <Button variant='warning' className='mx-2' onClick={() => history.back()}> Cancel </Button>
  });

  return (<>

<Row className="justify-content-md-center">
      <Col md="8" className="mx-auto">
        <MaroonCard title='New Account'>
        <h4 className='text-center'>Create New Account</h4>
         <hr className='py-2 bg-warning'/>

          {elements}

              <div className="d-flex justify-content-center align-items-center text-center mt-4">
                  <p className="form-text"> Already have an account? <Link to='/login' className="nav-link h4"> Login </Link></p> 
                    
                 
                </div>
        </MaroonCard>
      </Col>
    </Row>

</>  );
};