import history from '../utils/history';
import {useSiteContext } from '../utils/context';
import { Row, Col , Button} from 'react-bootstrap';
import MaroonCard from '../components/MaroonCard';
import { FormBuilder } from '../utils/boostrapForms';

const AddPost = () => {
  const context = useSiteContext()
  /*
    const handleSubmit = (event:any) => {
      event.preventDefault()
      const user_id = context.dbProfileState[0].uid
      const username = context.dbProfileState[0].username
      const data = {title: event.target.title.value,
                    body: event.target.body.value,
                    username: username,
                    uid: user_id}
  
      axios.post('/api/post/posttodb', data)
        .then(response => console.log(response))
        .catch((err) => console.log(err))
        .then(() => setTimeout(() => history.replace('/'), 700) )
    }
    */
  const handleCleanData = async (cleanData: any) => {

    const user_id = context.states?.auth?.state.profile!._id
    const username = context.states?.auth?.state.profile!.username
    const data = {
      title: cleanData.title,
      body: cleanData.body,
      username: username,
      id: user_id
    }

   const response = context.ajax!.post('posts/',data);
      console.log(response)
      setTimeout(() => history.replace('/'), 700)
  }

  return (
    <Row className="justify-content-md-center">
      <Col md="8" className="mx-auto">
        <MaroonCard title='New Post'>
          {FormBuilder({
            elements: [
              {
                lable: "Post Title",
                attrs: {
                  input: {
                    name: "title",
                    type: "text",
                    required: true,
                    placeholder: "Post title",
                    minLength: 3,
                    maxLength: 100
                  }
                }
              },
              {
                lable: "Post Body",
                attrs: {
                  input: {
                    name: "body",
                    required: true,
                    placeholder: "Post Body",
                    as: "textarea",
                    minLength: 3,
                    style: { height: '200px' }
                  }
                }
              },
            ],
            form: {
              attrs: {
                id: "addPostForm",
                name: "addPostForm",
              },
              cleandata: handleCleanData
            },
            addSubmit: true,
            extra: <Button variant='warning' className='mx-2' onClick={() => history.back()}> Cancel </Button>
          })}

        </MaroonCard>
      </Col>
    </Row>
  )
}



export default AddPost;
