import react from 'react';
import { Form, Button, FloatingLabel, InputGroup, FloatingLabelProps, InputGroupProps, FormGroupProps, FormControlProps, FormSelectProps, FormProps, FormCheckProps } from 'react-bootstrap';
import {getRandom, checkedUp, uuid, ProcessChanges, useProcessForm} from './html5validation'

interface InputAttrs extends FormControlProps {
  name?: string;
  required?: boolean;
  minLength?: number,
  maxLength?: number,
  pattern?: string,
  min?: number,
  max?: number,
}

interface BoostapFormProps {
  attrs?: FormProps,
  cleandata?: Function
}

interface BoostrapInputProps {
  IsCheckBox?: boolean;
  Floating?: FloatingLabelProps
  InputGroup?: InputGroupProps;
  FormGroup?: FormGroupProps;
  FormControl?: InputAttrs;
  FormCheck?: FormCheckProps;
}

interface FormBuilderElementProps {
  lable: string,
  isCheckbox?: boolean,
  attrs: {
    input?: InputAttrs,
    select?: BoostrapSelectProps
  }


}

interface FormBuilderProps {
  elements: FormBuilderElementProps[],
  form: BoostapFormProps,
  addSubmit?:boolean,
  extra?:JSX.Element
}

interface BoostrapSelectProps extends FormSelectProps {
  lable: string,
  options: React.DetailedHTMLProps<React.OptionHTMLAttributes<HTMLOptionElement>, HTMLOptionElement>[],
}

function BoostrapInput(props: BoostrapInputProps) {
  let input_control = props.IsCheckBox ? <Form.Check {...props.FormCheck} type='checkbox' /> : <Form.Control   {...props.FormControl} />;

  if (props.Floating) {
    return (
      <FloatingLabel controlId={getRandom()}   {...props.Floating}>
        {input_control}
      </FloatingLabel>
    );
  }
  else if (props.FormGroup) {
    return (
      <Form.Group controlId={getRandom()} {...props.FormGroup}>
        {input_control}
      </Form.Group>
    );
  }
  else if (props.InputGroup) {
    return (
      <InputGroup  {...props.InputGroup}>
        {<InputGroup.Text ><i className={ props.InputGroup!.itemProp}></i> </InputGroup.Text>}
        {input_control}
      </InputGroup>
    );
  }
  return (<></>)
}

function BoostrapSelect(props: BoostrapSelectProps) {

  return (<FloatingLabel controlId={getRandom()} label={props.lable} className="mb-3">
    <Form.Select {...props}>
      <option key={getRandom()} value="" >Select menu</option>
      {props.options.map(o => <option key={getRandom()} {...o}> {o.title} </option>)}
    </Form.Select>
  </FloatingLabel>);
}

function BoostrapForm(props: react.PropsWithChildren<BoostapFormProps>) {
  const { handleSubmit, validated, processChanges } = useProcessForm();

  return (
    <Form {...props.attrs} onChange={processChanges} noValidate validated={validated} onSubmit={(e: any) => handleSubmit(e, props.cleandata!)}>
      {props.children}
    </Form>
  )
}

function FormBuilder(props: FormBuilderProps) {

  let _elements = props.elements.map((item: FormBuilderElementProps) => {
    if (item.attrs.input) {
      return (item.isCheckbox ? <BoostrapInput IsCheckBox={true} FormGroup={{ className: "mb-3" }} FormCheck={{ name: item.attrs!.input!.name, label: item.lable, required: item.attrs!.input!.required }} /> : <BoostrapInput Floating={{ label: item.lable, className: "mb-3" }} FormControl={item.attrs!.input} />);
    } else if (item.attrs.select) {
      return <BoostrapSelect {...item.attrs.select!} />;
    } else {
      throw new Error('empty element no input or select data form formBuilder :' + item.lable)
    }
  })
  return (<BoostrapForm {...props.form}> {_elements} {props.addSubmit ? <Button type='submit' variant='primary'> submit </Button> : ""}{props.extra} </BoostrapForm>);
}

export { FormBuilder, uuid, getRandom, BoostrapForm, BoostrapInput, checkedUp, ProcessChanges, useProcessForm }